@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
header{width:100%; height:auto; background:cadetblue; color:#fff;}
header .center{display:flex; justify-content: space-between; padding:20px 0; align-items: center;}
header .logo a{color:#fff}
header .menu{width:30px; height:30px; border:1px solid #fff; display:flex; align-items: center; justify-content: center;}
header .menu i.fa{font-size:22px; color:#fff}
.detail{display:flex;flex-direction: column;}
.detail h2{font-size:25px;text-align: center;}
.detail img{align-items:center;}
.detail ul{padding: 20px 35px;  border: 10px solid rgba(84 156 154 / 67%);}
.detail ul h3{font-size:19px;text-align: center;}
.detail ul>li{border-bottom:1px solid rgba(230, 230, 230, 0.667);}
.App{display:flex; width:100%; height:auto; flex-direction: column;}
.App .search_form .center{width:100%; height:auto; display:flex; justify-content: center; padding:10px 0; border-bottom:1px solid cadetblue;}
.App .search_form input{padding:5px; border:1px solid cadetblue; width:60%;}
.App .search_form button{width:10%; background:cadetblue; color:#fff; border:0; margin-left:5px;}

.loader{width:100%;height:100vh;position:fixed;top:0;left:0;display:flex;align-items: center;justify-content: center;}
.loader img{width:40px;}
*{margin:0; padding:0; box-sizing: border-box;}
ol, ul{list-style:none;}
body{font-family: 'Roboto', sans-serif; font-size:14px;}
.center{width:90%; height:auto; margin:auto;}
.App .recipes{background:cadetblue; color:#fff;}
.App .recipes h2{font-size:40px;text-align: center;padding:10px 0; border: 2px solid #FFF;
margin-top: 11px;}
.App .recipes h2 a{color:#fff;}
.App .recipes img{width:100%;height:auto;}
